@keyframes ajaxify-lazy-rotate {
	100% {
		transform: rotate(360deg);
	}
}
.ajaxify-comments-spinner__wrapper {
	position: relative;
	background-color: var(--ajaxify-comments-spinner-container-background-color, #333 );
	padding: var( --ajaxify-comments-spinner-container-padding, 30px );

	.ajaxify-admin__preview-controls {
		position: absolute;
		top: 10px;
		right: 10px;
		background: #FFF;
	}

	.ajaxify-comments-spinner__label {
		color: var( --ajaxify-comments-spinner-label-color, #FFFFFF );
	}

}
.ajaxify-comments-spinner__wrapper.ajaxify-layout-left {
	
}
.ajaxify-comments-spinner__inner {
	display: flex;
	align-items: center;
	font-size: var( --ajaxify-comments-spinner-container-font-size, 32px );
	line-height: var( --ajaxify-comments-spinner-container-line-height, 1.25 );
	color: var( --ajaxify-comments-spinner-container-label-color, #FFFFFF );
}
.ajaxify-comments-spinner__icon {
	margin-right: var( --ajaxify-comments-spinner-icon-margin-right, 20px );
}
.ajaxify-comments-spinner__icon svg {
	display: block;
	animation: ajaxify-lazy-rotate var( --ajaxify-comments-spinner-icon-animation-speed, 1.25s ) linear infinite;
	color: var( --ajaxify-comments-spinner-icon-color, #FFFFFF );
	fill: var( --ajaxify-comments-spinner-icon-color, #FFFFFF );
	width: var( --ajaxify-comments-spinner-icon-size, 72px );
	height: var( --ajaxify-comments-spinner-icon-size, 72px );
}